const Song = ({ currentSong, isPlaying }) => {
  return (
    <div className="song-container">
      <img
        src={currentSong.cover}
        alt={currentSong.name}
        className={isPlaying ? 'rotateImg' : ''}
      />
      <h3>{currentSong.name}</h3>
      <h2>{currentSong.artist}</h2>
    </div>
  );
};

export default Song;
